___CSS_LOADER_EXPORT___.push([module.id, `.node-list-container {
  padding-top: 6rem;
  width: 70%;
}

.node-list-container h1 {
  padding: 8px;
  border-radius: 2px;
  color: var(--light-dark-color);
  text-align: center;
  margin-block-end: 2rem;
}

.node-list-container a:last-child {
  border-bottom-style: solid;
  border-bottom-color: var(--light-border);
  border-bottom-width: 1px;
}

@media screen and (min-width: 320px) and (max-width: 565px) {
  .node-list-container {
    width: 95%;
    margin: 0 auto;
    padding-top: 2rem;
  }
}

@media screen and (min-width: 320px) and (max-width: 450px) {
  .node-list-container h1 {
    text-align: start;
    width: auto;
  }
}

.carousel-filter select {
  height: 30px;
  text-align: center;
  border: 1px solid var(--light-border);
  background: unset;
  cursor: pointer;
}

.carousel-filter select option:hover {
  background-color: var(--light-border);
}

.carousel-filter {
  text-align: end;
  padding-bottom: 5px;
  display: flex;
}

.custom-dropdown {
  position: relative;
  cursor: pointer;
  transition: all 0.5s ease;
}

.selected-option {
  display: block;
  padding: 3.5px 0;
  background-color: var(--white-color);
  width: 175px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span.selected-option:after {
  content: '\\e5cf';
  font-family: Material Symbols Outlined;
  color: var(--light-dark-color);
  font-size: 22px;
}

.options-list {
  list-style: none;
  padding: 0;
  margin: 0;
  position: absolute;
  top: 100%;
  left: 0;
  display: none;
  background-color: var(--white-color);
  z-index: 4;
  transition: all 0.5s ease;
}

.custom-dropdown.open .options-list {
  display: block;
  transition: all 0.5s ease;
}

.option {
  padding: 7px 4px 7px 16px;
  cursor: pointer;
  width: 175px;
  text-align: start;
  transition: all 0.5s ease;
}

.option:hover {
  background-color: var(--light-dark-color);
  color: var(--white-color);
  transition: all 0.5s ease;
}
